<template lang="pug">
    div.alipay-cash-content
        div.input-info
            input(type="text" v-model="alipay_name" placeholder="请输入支付宝名称")
        div.input-info
            input(type="text" v-model="alipay_account" placeholder="请输入支付宝账号")
        div.btn-group
            div.btn-confirm(@click="submit") 确认
</template>
<script>
import queryString from 'querystring'
import { Toast } from 'mint-ui'
export default {
    name: 'profitAlipayCash',
    data() {
        return {
            alipay_name: '',
            alipay_account: ''
        }
    },
    methods: {
        check() {
            // 验证表单
            if (!$.trim(this.alipay_name)) {
                Toast('请输入支付宝名称');
                return false;
            }
            else if (!$.trim(this.alipay_account)) {
                Toast('请输入支付宝账号');
                return false;
            }
            else {
                return true;
            }
        },
        submit() {
            if (this.check()) {
                let axiosUrl = this.api.userBindingAlipay();
                this.axios.post(axiosUrl, queryString.stringify({
                    alipay_name: this.alipay_name,
                    alipay_account: this.alipay_account
                })).then(res => {
                    // 请求成功   状态  status 1/0   1:成功  0:失败
                    if (res.data.status == 1) {
                        // 成功
                        let instance = Toast(res.data.error || '操作成功');
                        setTimeout(() => {
                            instance.close();
                            this.$router.push('/user/profit/withdrawals');
                        }, 2000);
                    }
                    else {
                        Toast(res.data.error || '操作失败');
                    }
                })
                    .catch(err => {
                        // 请求失败
                        Toast(err || '请求失败');
                    });
            }
        },
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.alipay-cash-content {
    padding: 0 .75rem;
    .input-info {
        height: 1.8rem;
        width: 100%;

        margin-top: .85rem;
        background-color: #fff;
        input {
            height: 100%;
            width: 100%;
            padding-left: .5rem;
            border: 0;
            -webkit-appearance: none;
            outline: none;
            border: 1px solid #d3d3d3;
            border-radius: .3rem;
        }
    }
    .btn-group {
        margin-top: 1.65rem;
        padding: 0 .5rem;
        .btn-confirm {
            height: 2rem;
            text-align: center;
            line-height: 2rem;
            background-color: @color-theme;
            color: #fff;
            border-radius: 1rem;
        }
    }
}
</style>



